<template>
    <v-card class="mx-auto" max-width="500" outlined>
        <v-card-title>
            ورود
        </v-card-title>

        <v-form v-model="formValid">
            <v-text-field
                class="mx-3"
                outlined
                label="ایمیل"
                color="#d22d7f"
                type="email"
                prepend-inner-icon="mdi-email"
                v-model="form.email"
                :rules="[required, checkEmail]"
            ></v-text-field>
            <v-text-field
                class="mx-3"
                outlined
                label="پسورد"
                v-model="form.password"
                color="#d22d7f"
                :type="showPassword ? 'text' : 'password'"
                :prepend-inner-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                @click:prepend-inner="showPassword = !showPassword"
                :rules="[(value) => required(value, 'پسورد'), strongPassword]"
            ></v-text-field>

            <v-card-actions>
                <v-row>
                    <v-col cols="12">
                        <v-btn
                            class="mb-1"
                            rounded
                            block
                            color="primary"
                            :loading="loading"
                            :disabled="disabledBtn"
                            @click="submitForm"
                        >
                            ورود
                        </v-btn>
                    </v-col>
                    <v-col cols="12">
                        <v-btn block  rounded dark :to="{ name: 'Landing' }">
                            بازگشت به صفحه ی اصلی</v-btn
                        >
                    </v-col>
                </v-row>
            </v-card-actions>
        </v-form>
    </v-card>
</template>

<script>
import { checkEmail, required, strongPassword, min8 } from '../validation/index';
export default {
    data() {
        return {
            showPassword: false,
            form: {
                email: '',
                password: '',
            },
            loading: false,
            required,
            checkEmail,
            strongPassword,
            formValid: false,
        };
    },
    computed: {
        disabledBtn() {
            return this.loading || !this.formValid;
        },
    },
    methods: {
        submitForm() {
            if (this.formValid) {
                this.loading = true;
                localStorage.setItem('user', JSON.stringify(this.form));
                setTimeout(() => {
                    this.loading = false;
                    this.$router.push({ name: 'Dashboard' });
                }, 2000);
            }
        },
    },
};
</script>
